  --dropdown-content-max-height: 200px;
}

.autocomplete {
  position: relative;
}
.autocomplete .dropdown-menu {
  display: block;
  width: 100%;
}
.autocomplete .dropdown-menu.is-opened-top {
  top: auto;
  bottom: 100%;
}
.autocomplete .dropdown-content {
  overflow: auto;
  max-height: var(--dropdown-content-max-height);
}
.autocomplete .dropdown-item, .autocomplete .dropdown .dropdown-menu .has-link a, .dropdown .dropdown-menu .has-link .autocomplete a {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.autocomplete .dropdown-item.is-hovered, .autocomplete .dropdown .dropdown-menu .has-link a.is-hovered, .dropdown .dropdown-menu .has-link .autocomplete a.is-hovered {
  background: var(--dropdown-item-hover-background-color, var(--background, whitesmoke));
  color: var(--dropdown-item-hover-color, var(--scheme-invert, #0a0a0a));
}
.autocomplete .dropdown-item.is-disabled, .autocomplete .dropdown .dropdown-menu .has-link a.is-disabled, .dropdown .dropdown-menu .has-link .autocomplete a.is-disabled {
  opacity: 0.5;
  cursor: not-allowed;
}
.autocomplete.is-small {
  border-radius: var(--control-radius-small);
  font-size: var(--size-small, 0.75rem);
}
.autocomplete.is-medium {
  font-size: var(--size-medium, 1.25rem);
}
.autocomplete.is-large {
  font-size: var(--size-large, 1.5rem);
}

:root {
  --carousel-min-height: 120px;
  --carousel-arrow-background: var(--scheme-main, white);
  --carousel-arrow-color: var(--primary, #7958d5);
  --carousel-arrow-icon-spaced: 1.5rem;
  --carousel-arrow-top: 50%;
  --carousel-indicator-background: rgba(var(--scheme-invert-rgb, 10, 10, 10), 0.5);
  --carousel-indicator-border: var(--scheme-main, white);
  --carousel-indicator-color: var(--primary, #7958d5);
  --carousel-indicator-spaced: 0.5rem;
  --carousel-overlay-background: rgba(var(--scheme-invert-rgb, 10, 10, 10), 0.86);
  --carousel-overlay-z: 40;
}

.carousel {
  min-height: var(--carousel-min-height);
  position: relative;
}
.carousel.is-overlay {
  background-color: var(--carousel-overlay-background);
  align-items: center;
  flex-direction: column;
  justify-content: center;
  display: flex;
  max-height: 100vh;
  position: fixed;
  z-index: var(--carousel-overlay-z, 40);
}
.carousel.is-overlay .carousel-item img {
  cursor: default;
}
.carousel.is-overlay .carousel-indicator.has-background {
  background: transparent;
}
.carousel .progress, .carousel .progress-wrapper.is-not-native {
  border-radius: var(--radius-small, 2px);
  height: 0.25rem;
  margin-bottom: 0;
}
.carousel .carousel-items {
  position: relative;
  display: flex;
  overflow: hidden;
  width: 100%;
}
@media screen and (min-width: 769px), print {
  .carousel .carousel-items:hover .carousel-arrow.is-hovered {
    opacity: 1;
  }
}
.carousel .carousel-items .carousel-item {
  flex-shrink: 0;
  width: 100%;
}
.carousel .carousel-pause {
  pointer-events: none;
  position: absolute;
  top: 0;
  right: 0.15rem;
  z-index: 1;
}
.carousel .carousel-indicator {
  width: 100%;
  padding: var(--carousel-indicator-spaced, 0.5rem);
  display: flex;
  align-items: center;
  justify-content: center;
}
.carousel .carousel-indicator.has-background {
  background: var(--carousel-indicator-background);
}
.carousel .carousel-indicator.has-custom {
  flex-wrap: nowrap;
  justify-content: flex-start;
  -webkit-overflow-scrolling: touch;
  overflow: hidden;
  overflow-x: auto;
}
.carousel .carousel-indicator.has-custom.is-small .indicator-item {
  flex: 1 0 10%;
}
.carousel .carousel-indicator.has-custom.is-medium .indicator-item {
  flex: 1 0 16.66667%;
}
.carousel .carousel-indicator.is-inside {
  position: absolute;
}
.carousel .carousel-indicator.is-inside.is-bottom {
  bottom: 0;
}
.carousel .carousel-indicator.is-inside.is-top {
  top: 0;
}
.carousel .carousel-indicator .indicator-item:not(:last-child) {
  margin-right: var(--carousel-indicator-spaced, 0.5rem);
}
.carousel .carousel-indicator .indicator-item.is-active .indicator-style,
.carousel .carousel-indicator .indicator-item .indicator-style:hover {
  background: var(--carousel-indicator-color);
  border: 1px solid var(--carousel-indicator-border);
}
.carousel .carousel-indicator .indicator-item .indicator-style {
  display: block;
  border: 1px solid var(--carousel-indicator-color);
  background: var(--carousel-indicator-border);
  outline: none;
  transition-property: all;
  transition-duration: calc(var(--speed, 86ms) * 2);
  transition-timing-function: var(--easing, ease-out);
}
.carousel .carousel-indicator .indicator-item .indicator-style.is-boxes {
  height: 10px;
  width: 10px;
}
.carousel .carousel-indicator .indicator-item .indicator-style.is-dots {
  border-radius: var(--radius-rounded, 9999px);
  height: 10px;
  width: 10px;
}
.carousel .carousel-indicator .indicator-item .indicator-style.is-lines {
  height: 5px;
  width: 25px;
}

.carousel-list {
  position: relative;
  overflow: hidden;
  width: 100%;
}
.carousel-list.has-shadow {
  box-shadow: 0px 0px 10px rgba(var(--scheme-invert-rgb, 10, 10, 10), 0.25);
}
@media screen and (min-width: 769px), print {
  .carousel-list:hover .carousel-arrow.is-hovered {
    opacity: 1;
  }
}
.carousel-list .carousel-slides {
  position: relative;
  display: flex;
  width: 100%;
}
.carousel-list .carousel-slides:not(.is-dragging) {
  transition-property: all;
  transition-duration: calc(var(--speed, 86ms) * 3);
  transition-timing-function: ease-out;
}
.carousel-list .carousel-slides.has-grayscale .carousel-slide img {
  filter: grayscale(100%);
}
.carousel-list .carousel-slides.has-grayscale .carousel-slide.is-active img {
  filter: grayscale(0%);
}
.carousel-list .carousel-slides.has-opacity .carousel-slide img {
  opacity: 0.25;
}
.carousel-list .carousel-slides.has-opacity .carousel-slide.is-active img {
  opacity: 1;
}
.carousel-list .carousel-slides .carousel-slide {
  border: 2px solid transparent;
  flex-shrink: 0;
}

.carousel-arrow {
  transition-property: all;
  transition-duration: calc(var(--speed, 86ms) * 2);
  transition-timing-function: var(--easing, ease-out);
}
.carousel-arrow.is-hovered {
  opacity: 0;
}
.carousel-arrow .icon {
  background: var(--carousel-arrow-background);
  color: var(--carousel-arrow-color);
  cursor: pointer;
  border: 1px solid var(--carousel-arrow-background);
  border-radius: var(--radius-rounded, 9999px);
  outline: 0;
}
.carousel-arrow .icon:hover {
  border: 1px solid var(--carousel-arrow-color);
  opacity: 1;
}
.carousel-arrow .icon.has-icons-left, .carousel-arrow .icon.has-icons-right {
  position: absolute;
  top: var(--carousel-arrow-top, 50%);
  transform: translateY(calc(-1 * var(--carousel-arrow-top, 50%)));
  z-index: 1;
}
.carousel-arrow .icon.has-icons-left {
  left: var(--carousel-arrow-icon-spaced, 1.5rem);
}
.carousel-arrow .icon.has-icons-right {
  right: var(--carousel-arrow-icon-spaced, 1.5rem);
}

